import React from "react";
import { Box, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import {
  VerticalTimeline,
  VerticalTimelineElement,
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import { FaLaptopCode } from "react-icons/fa";
import { MdWork } from "react-icons/md";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    background: "#303f4a",
  },
  paper: {
    padding: "10px",
    margin: "10px",
  },
  Typography: {
    color: "white",
    fontFamily: "Trocchi",
  },
}));

const Experience = (props) => {
  const classes = useStyles();
  const experience = props.experience;

  return (
    <Box width="100%" className={classes.root} id="experience">
      <Typography variant="h2" className={classes.Typography}>
        Experience
      </Typography>
      <VerticalTimeline animate={true} layout={"2-columns"}>
        {experience.map((experience_record, index) => {
          return (
            <VerticalTimelineElement
              key={index}
              className="vertical-timeline-element--work"
              contentArrowStyle={{
                borderRight: "7px solid  rgb(33, 150, 243)",
              }}
              iconStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
              // contentStyle={{ background: "rgb(33, 150, 243)", color: "#fff" }}
              icon={<MdWork />}
            >
              <h3 className="vertical-timeline-element-title">
                {experience_record.Designation}
              </h3>
              <h4 className="vertical-timeline-element-subtitle">
                {experience_record.CompanyName +
                  " - " +
                  experience_record.UnitName}
              </h4>
              <p>{experience_record.Description}</p>
              <span
                className="vertical-timeline-element-date"
                style={{ color: "white" }}
              >
                {experience_record.From + " - " + experience_record.To}
              </span>
            </VerticalTimelineElement>
          );
        })}
        <VerticalTimelineElement
          iconStyle={{ background: "rgb(16, 204, 82)", color: "#fff" }}
          icon={<FaLaptopCode />}
        />
      </VerticalTimeline>
      {/* </Paper> */}
    </Box>
  );
};

export default Experience;
