import React, { useState } from "react";
import { FaCode, FaReact, FaStar, FaArrowAltCircleUp } from "react-icons/fa";
import { Link } from "react-router-dom";

const myScrollFunc = function (ele) {
  let offsetTop = document.getElementById(ele).offsetTop;
  window.scrollTo({
    top: offsetTop - 40,
    behavior: "smooth",
  });
};

const Footer = () => {
  const [bgStyle] = useState({ backgroundColor: "#f5f5f5" });

  return (
    <footer style={bgStyle} className="footer">
      <FaCode className="react-icons"></FaCode>
      by{" "}
      <a
        className="badge badge-dark"
        rel="noopener"
        href="https://github.com/aditya-bhat"
        aria-label="My GitHub"
      >
        Aditya D. Bhat
      </a>{" "}
      and{" "}
      <a
        className="badge badge-dark"
        rel="noopener"
        href="https://github.com/raajeive"
        aria-label="My GitHub"
      >
        Raajeive K
      </a>{" "}
      using <FaReact className="react-icons"></FaReact>{" "}
      <Link
        to={""}
        onClick={() => {
          myScrollFunc("navbar");
        }}
      >
        Scroll to Top!{" "}
      </Link>{" "}
      <FaArrowAltCircleUp className="react-icons"></FaArrowAltCircleUp>
      <p>
        <small className="text-muted">
          {" "}
          This Project code is open source. Feel free to fork and make your own
          version. If you like the{" "}
          <a
            className="badge badge-dark"
            rel="noopener"
            href="https://github.com/aditya-bhat/devPortfolio"
            aria-label="My GitHub"
          >
            project
          </a>
          , please leave a{" "}
        </small>{" "}
        <FaStar className="react-icons"></FaStar>{" "}
      </p>
    </footer>
  );
};

export default Footer;
